  background-color: #0066a1;
  color: white;
  font-weight: 500;
  text-align: center; }
  .caption-text--link {
    text-decoration: none; }
    .caption-text--link:hover {
      text-decoration: underline; }

.img {
  margin-left: auto;
  margin-right: auto;
  border-radius: 4px;
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0; }

.blow-up {
  display: block;
  background-color: rgba(0, 160, 236, 0.2);
  left: 0px;
  top: 0px;
  height: 0;
  width: 0;
  overflow: hidden;
  visibility: hidden;
  position: fixed; }
  .blow-up, .blow-up * {
    box-sizing: border-box; }
  .blow-up--visible {
    z-index: 10;
    visibility: visible;
    overflow: visible;
    width: 100vw;
    height: 100vh; }
  .blow-up__img {
    border: 5px #005284;
    display: block;
    margin-left: auto;
    margin-right: auto;
    margin-top: 5vh;
    width: 70vw;
    min-width: 300px;
    max-width: 650px; }
  .blow-up__caption-text {
    box-shadow: 0 5px 5px rgba(0, 82, 132, 0.7);
    font-size: 20px;
    width: 70vw;
    min-width: 300px;
    max-width: 650px; }

.image-box {
  height: 100%; }
  .image-box, .image-box > * {
    margin: 0;
    box-sizing: border-box; }
  .image-box__img {
    margin-top: 0;
    width: 100%;
    display: block; }
  .image-box__caption-text {
    font-size: 16px; }
</style>
